.app__resume{
    flex:1;
    width: 100%;
    flex-direction: column;
}
.app__resume-content{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.app__resume-img{
    justify-content: center;
    display: flex;
    width: 67%;
    padding:1rem,1rem;
    button{
        border: none;
    }
    img{
        width: 100%;
        height: 100%;
        border-radius: 0.5rem;
        cursor: pointer;
    }
}