.app__work{
    flex:1;
    width:100%;
    flex-direction: column;
}

.app__work-portfolio{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;

    .app__work-item{
        width: 67%;
        flex-direction: column;
        margin: 2rem;
        padding: 1rem;
        border-radius: 0.5rem;
        background-color: white;
        color: black;

        cursor: pointer;
        transition: all 0.3s ease;

        &:hover{
            box-shadow: 0,0,25px,rgba(0,0,0,.2);
        }
        @media screen and (min-width:2000px) {
            padding: 1.25rem;
            border-radius: 0.75rem;
        }
        @media screen and (max-width:300px) {
            width: 100%;
            margin: 1rem;
        }
    }
}
.app__work-img{
    width: 100%;
    height: 50px;
    border: 1px solid black;
    border-radius: 10px;

    position: relative;
    img{
        width: 100%;
        height: 100%;
        border-radius: 0.5rem;
        object-fit: cover;
    }
    a{
        text-decoration: none;
    }
    @media screen and (min-width:2000px) {
        height: 100px;
    }
}

.app__work-hover{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(127.5,127.5,127.5,1);
    
    border-radius: 0.5rem;
    opacity: 0;
    transition: all 0.3s ease;

    div{
        width: 50px;
        height: 50px;
        border-radius: 50%;
        background-color: rgba(0,0,0,.5);
        margin: 1;
        font-family: var(--font-base);
        font-weight: 800;
        cursor: pointer;
        transition: all 0.3s ease;

        svg{
            width: 50%;
            height: 50%;
            color: var(--white-color);
        }
    }
    img{
        width:100%;
    }
}

.app__work-content{
    padding: 0.5rem;
    width: 100%;
    position: relative;
    flex-direction: column;
    h4{
        margin-top: 1rem;
        line-height: 1.5;
    }
}
.hover-out{
    opacity: 1;
    &:hover{
        opacity: 0; 
    }
}